<template>
  <div>
    <el-row v-loading="loading">
      <el-col :span="24">
        <div style="text-align: left">
          <el-button
            type="text"
            icon="el-icon-back"
            @click="goBack"
            style="padding-bottom: 0px"
            >返回</el-button
          >
        </div>
      </el-col>
      <img :src="this.$route.query.img" >
    </el-row>

    <el-row style="margin-bottom: 20px" :gutter="10">
      <el-col :span="18">
        <el-input
          type="text"
          v-model="comment.content"
          placeholder="添加评论"
        ></el-input>
      </el-col>
      <el-col :span="6">
        <el-button
          type="primary"
          icon="el-icon-search"
          size="mini"
          style="margin-left: 3px"
          @click="addComment"
          >添加评论
        </el-button>
      </el-col>
    </el-row>
    <!-- 评论区 -->
    <div
      style="text-align: left"
      v-for="(comment, index) in comments"
      :key="index"
    >
      <div class="commentCon">{{ comment.content }}</div>
      <el-divider content-position="left">{{
        formatDate(comment.publishDate)
      }}</el-divider>
      <el-divider prop="username" content-position="left"
        >{{ comment.username }} &numsp;&numsp;
        <el-button
          v-show="canShow(comment.username)"
          @click="chehui(comment.cid)"
          >撤回</el-button
        >
      </el-divider>

      <el-divider><i class="el-icon-mobile-phone"></i></el-divider>
    </div>
    <el-pagination
      background
      :page-size="pageSize"
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="currentChange"
      v-show="this.comments.length > 0"
    >
    </el-pagination>
  </div>

  
</template>
 <style scoped>
.commentCon {
  width: 100%;
  word-wrap: break-word;
}
</style>
<script>
import { getRequest, putRequest } from '../utils/api'
import moment from 'moment'
import { postRequest } from '../utils/api'
export default {
  mounted: function () {
    // var img = this.$route.query.img;
    var _this = this;
    this.loading = true;
    
    this.loadComments(this.currentPage, this.pageSize);
    getRequest("/currentUserName").then(function (msg) {
      _this.currentUserName = msg.data;
    }, function (msg) {
      _this.currentUserName = '游客';
    });
    getRequest("/currentUserId").then(function (msg) {
      _this.uid = msg.data;
    }, function (msg) {
    });
  },
  methods: {
    chehui (id) {
      console.log(id)
      var _this = this;
      var url = "/deleteMyThisComment";
      var mid = this.$route.query.mid;
      putRequest(url, { cid: id, uid: _this.uid, mid: mid }).then(resp => {
        var data = resp.data;
        if (data.status == 'success') {
          _this.$alert(data.msg);
          _this.loadComments(this.currentPage, this.pageSize);
        } else {
          _this.$alert(data.msg);
        }
      })

    },
    canShow (name) {
      var currentUserName = this.currentUserName;
      if (name == currentUserName) {
        return true;
      }
      else {
        return false;
      }
    },
    // 格式化时间
    formatDate (time) {
      return moment(time).format('YYYY-MM-DD HH:mm:ss')
    },
    loadComments (page, count) {
      var id = this.$route.query.mid;
      var _this = this;
      getRequest("/getComments/" + id + "?page=" + page + "&count=" + count).then(resp => {
        if (resp.status == 200) {
          _this.comments = resp.data.comments;
          _this.totalCount = resp.data.totalCount;
        }
        _this.loading = false;
      }, resp => {
        _this.loading = false;

      });
    },

    currentChange (currentPage) {
      this.currentPage = currentPage;
      this.loading = true;
      this.loadComments(currentPage, this.pageSize);
    },
    addComment () {
      var mid = this.$route.query.mid;
      var _this = this;
      postRequest('/addComment', {
        content: this.comment.content,
        mid: mid
      }).then(resp => {
        _this.loading = false;
        if (resp.status == 200) {
          if (resp.data.status == 'success') {
            _this.$alert(resp.data.msg, '成功!');
            _this.loadComments(this.currentPage, this.pageSize);
          } else {
            _this.$alert(resp.data.msg, '失败!');
          }
        } else {
          _this.$alert(resp.data.msg, '失败!');
        }
      }, resp => {
        _this.loading = false;
        _this.$alert('找不到服务器⊙﹏⊙∥!', '失败!');
      });
    },
    goBack () {
      this.$router.go(-1);
    }
  },
  data () {
    return {
      uid: -1,
      currentUserName: '',
      comment: {
        content: ''
      },
      article: {

      },
      loading: false,
      currentPage: 1,
      pageSize: 2,
      totalCount: -1,
      activeName: '',
      comments: {

      }
    }
  }
}
</script>



