<template>
  <el-container class="menu_list">
    <el-main class="main">
      <el-tabs @tab-click="handleClick" type="border-card">
        <el-tab-pane label="热菜" name="recai">
          <menu_table
            state="0"
            :sumPrice="this.sumPrice"
            ref="recaitable"
            @getrecai="getrecai"
            @getrecaiprice="getrecaiprice"
            @getrecaicost="getrecaicost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="凉菜" name="liangcai">
          <menu_table
            state="1"
            :sumPrice="this.sumPrice"
            ref="liangcaitable"
            @getliangcai="getliangcai"
            @getliangcaiprice="getliangcaiprice"
            @getliangcaicost="getliangcaicost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="汤羹" name="tanggeng">
          <menu_table
            state="2"
            :sumPrice="this.sumPrice"
            ref="tanggengtable"
            @gettanggeng="gettanggeng"
            @gettanggengprice="gettanggengprice"
            @gettanggengcost="gettanggengcost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="主食" name="zhushi">
          <menu_table
            state="3"
            :sumPrice="this.sumPrice"
            ref="zhushitable"
            @getzhushi="getzhushi"
            @getzhushiprice="getzhushiprice"
            @getzhushicost="getzhushicost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="小吃" name="xiaochi">
          <menu_table
            state="4"
            :sumPrice="this.sumPrice"
            ref="xiaochitable"
            @getxiaochi="getxiaochi"
            @getxiaochiprice="getxiaochiprice"
            @getxiaochicost="getxiaochicost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="西餐" name="xican">
          <menu_table
            state="5"
            :sumPrice="this.sumPrice"
            ref="xicantable"
            @getxican="getxican"
            @getxicanprice="getxicanprice"
            @getxicancost="getxicancost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="烘焙" name="hongbei">
          <menu_table
            state="6"
            :sumPrice="this.sumPrice"
            ref="hongbeitable"
            @gethongbei="gethongbei"
            @gethongbeiprice="gethongbeiprice"
            @gethongbeicost="gethongbeicost"
          ></menu_table>
        </el-tab-pane>
        <el-tab-pane label="饮品" name="yinpin">
          <menu_table
            state="7"
            :sumPrice="this.sumPrice"
            ref="yinpintable"
            @getyinpin="getyinpin"
            @getyinpinprice="getyinpinprice"
            @getyinpincost="getyinpincost"
          ></menu_table>
        </el-tab-pane>
      </el-tabs>
    </el-main>
    <el-footer>
      <el-tag
        :key="index"
        v-for="(tag, index) in recai"
        :disable-transitions="false"
        style="margin-left: 20px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in liangcai"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in tanggeng"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in zhushi"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in xiaochi"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in xican"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in hongbei"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <el-tag
        :key="tag"
        v-for="tag in yinpin"
        :disable-transitions="false"
        style="margin-left: 10px"
      >
        {{ tag }}
      </el-tag>
      <!-- Form -->

      <el-dialog title="订单信息" :visible.sync="dialogFormVisible">
        <el-form :model="usermsg">
          <el-form-item label="用户名">
            <el-input v-model="usermsg.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="手机号">
            <el-input v-model="usermsg.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="联系地址">
            <el-select
              v-model="curaddress"
              filterable
              allow-create
              default-first-option
              clearable
              placeholder="请选择活动区域"
            >
              <el-option
                v-for="(item, index) in usermsg.addresses"
                :key="index"
                :value="item.address"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="updateAddress(curaddress)"
            >确 定</el-button
          >
        </div>
      </el-dialog>
      <el-descriptions title="用户信息">
        <el-descriptions-item label="用户名">{{
          usermsg.username
        }}</el-descriptions-item>
        <el-descriptions-item label="手机号">{{
          usermsg.phone
        }}</el-descriptions-item>

        <el-descriptions-item label="联系地址">{{
          curaddress
        }}</el-descriptions-item>
      </el-descriptions>

      <el-col :span="12"></el-col>
      <el-button type="danger" size="mini" @click="clearmenus"
        >清空重新选菜</el-button
      >
      <el-button type="danger" size="mini" @click="dialogFormVisible = true"
        >修改个人信息</el-button
      >
      <el-button type="primary" size="mini" @click="submitMenus"
        >提交订单</el-button
      >
      <br />
    </el-footer>
  </el-container>
</template>
<script>
import MenuTable from '@/components/MenuTable'
import { getRequest } from '../utils/api'
import { postRequest } from '../utils/api'
export default {
  mounted: function () {
    var _this = this;
    getRequest("/isAdmin").then(resp => {
      if (resp.status == 200) {
        _this.isAdmin = resp.data;
      }
    });
    getRequest("/currentUserMsg").then(function (msg) {
      _this.usermsg = msg.data;
      _this.curaddress = msg.data.addresses[0].address;
    }, function (msg) {

    });
  },
  data () {
    return {
      centerDialogVisible: false,
      // : 'post',
      recai: [],
      liangcai: [],
      tanggeng: [],
      zhushi: [],
      xiaochi: [],
      xican: [],
      hongbei: [],
      yinpin: [],

      recaiprice: [],
      liangcaiprice: [],
      tanggengprice: [],
      zhushiprice: [],
      xiaochiprice: [],
      xicanprice: [],
      hongbeiprice: [],
      yinpinprice: [],
      sumPrice: 0,

      recaicost: [],
      liangcaicost: [],
      tanggengcost: [],
      zhushicost: [],
      xiaochicost: [],
      xicancost: [],
      hongbeicost: [],
      yinpincost: [],
      sumCost: 0,


      sumTitles: [],
      aid: [],
      stateitems: [0, 1, 2, 3, 4, 5, 6, 7],
      isAdmin: false,
      //
      usermsg: {
        username: '',
        phone: '',
        addresses: '',
      },
      curaddress: '',
      dialogFormVisible: false,
      formLabelWidth: '120px'
    };
  },
  methods: {
    clearmenus () {
      this.$refs.recaitable.deleteRow()
      this.$refs.liangcaitable.deleteRow()
      this.$refs.tanggengtable.deleteRow()
      this.$refs.zhushitable.deleteRow()
      this.$refs.xiaochitable.deleteRow()
      this.$refs.xicantable.deleteRow()
      this.$refs.hongbeitable.deleteRow()
      this.$refs.yinpintable.deleteRow()
    },
    submitMenus () {
      var _this = this;
      this.$confirm('是否提交订单?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        _this.loading = true;

        var content = '';
        for (var i = 0; i < this.recai.length; i++) {
          content += this.recai[i];
          content += '+';
        }
        for (var i = 0; i < this.liangcai.length; i++) {
          content += this.liangcai[i];
          content += '+';
        }
        for (var i = 0; i < this.tanggeng.length; i++) {
          content += this.tanggeng[i];
          content += '+';
        }
        for (var i = 0; i < this.zhushi.length; i++) {
          content += this.zhushi[i];
          content += '+';
        }
        for (var i = 0; i < this.xiaochi.length; i++) {
          content += this.xiaochi[i];
          content += '+';
        }
        for (var i = 0; i < this.xican.length; i++) {
          content += this.xican[i];
          content += '+';
        }
        for (var i = 0; i < this.hongbei.length; i++) {
          content += this.hongbei[i];
          content += '+';
        }
        for (var i = 0; i < this.yinpin.length; i++) {
          content += this.yinpin[i];
          content += '+';
        }
        var cont = content.substr(0, content.length - 1);
        console.log(cont);
        postRequest('/addOrder', {
          address: _this.curaddress,
          content: cont,
          customer: _this.usermsg.username,
          price: _this.sumPrice,
          cost: _this.sumCost,
          phone: _this.usermsg.phone

        }).then(resp => {
          if (resp.status == 200) {
            var data = resp.data;
            _this.$message({ type: data.status, message: data.msg });
            if (data.status == 'success') {
              window.bus.$emit('blogTableReload')//通过选项卡都重新加载数据
              this.clearmenus()
            }
          } else {
            _this.$message({ type: 'error', message: '提交失败!' });
          }
          _this.loading = false;
        }, resp => {
          _this.loading = false;
          _this.$message({ type: 'error', message: '提交失败!' });
        });
      }).catch(() => {
        _this.$message({
          type: 'info',
          message: '已取消提交'
        });
      });
    },
    updateAddress (address) {
      this.dialogFormVisible = false;
      var _this = this;
      postRequest('/addOneAddress', {
        address: _this.curaddress
      }).then(resp => {
        if (resp.status == 200) {
          if (resp.data.status == 'success') {
          } else {

          }
        } else {
        }
      })
    },

    //删除菜品
    // handleClose0(tag) {
    //   this.recai.splice(this.recai.indexOf(tag), 1);
    //   this.$refs.recaitable.deleteRow(tag);
    // },
    //获取菜品
    getrecai (data) {
      this.recai = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();
      })
    },

    getliangcai (data) {
      this.liangcai = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },
    gettanggeng (data) {
      this.tanggeng = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },
    getzhushi (data) {
      this.zhushi = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },
    getxiaochi (data) {
      this.xiaochi = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },
    getxican (data) {
      this.xican = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })

    },
    gethongbei (data) {
      this.hongbei = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },
    getyinpin (data) {
      this.yinpin = data;
      this.$nextTick(() => {
        this.loadSumprice();
        this.loadSumcost();

      })
    },


    getrecaiprice (data) {
      this.recaiprice = data;
    },
    getliangcaiprice (data) {
      this.liangcaiprice = data;
    },
    gettanggengprice (data) {
      this.tanggengprice = data;
    },
    getzhushiprice (data) {
      this.zhushiprice = data;
    },
    getxiaochiprice (data) {
      this.xiaochiprice = data;
    },
    getxicanprice (data) {
      this.xicanprice = data;
    },
    gethongbeiprice (data) {
      this.hongbeiprice = data;
    },
    getyinpinprice (data) {
      this.yinpinprice = data;
    },

    getrecaicost (data) {
      this.recaicost = data;
    },
    getliangcaicost (data) {
      this.liangcaicost = data;
    },
    gettanggengcost (data) {
      this.tanggengcost = data;
    },
    getzhushicost (data) {
      this.zhushicost = data;
    },
    getxiaochicost (data) {
      this.xiaochicost = data;
    },
    getxicancost (data) {
      this.xicancost = data;
    },
    gethongbeicost (data) {
      this.hongbeicost = data;
    },
    getyinpincost (data) {
      this.yinpincost = data;
    },

    loadSumprice () {
      var sum = 0;
      for (var i = 0; i < this.recaiprice.length; i++) {
        sum += this.recaiprice[i];
      }
      for (var i = 0; i < this.liangcaiprice.length; i++) {
        sum += this.liangcaiprice[i];
      }
      for (var i = 0; i < this.tanggengprice.length; i++) {
        sum += this.tanggengprice[i];
      }
      for (var i = 0; i < this.zhushiprice.length; i++) {
        sum += this.zhushiprice[i];
      }
      for (var i = 0; i < this.xiaochiprice.length; i++) {
        sum += this.xiaochiprice[i];
      }
      for (var i = 0; i < this.xicanprice.length; i++) {
        sum += this.xicanprice[i];
      }
      for (var i = 0; i < this.hongbeiprice.length; i++) {
        sum += this.hongbeiprice[i];
      }
      for (var i = 0; i < this.yinpinprice.length; i++) {
        sum += this.yinpinprice[i];
      }
      this.sumPrice = sum;
    },

    loadSumcost () {
      var sum = 0;
      for (var i = 0; i < this.recaicost.length; i++) {
        sum += this.recaicost[i];
      }
      for (var i = 0; i < this.liangcaicost.length; i++) {
        sum += this.liangcaicost[i];
      }
      for (var i = 0; i < this.tanggengcost.length; i++) {
        sum += this.tanggengcost[i];
      }
      for (var i = 0; i < this.zhushicost.length; i++) {
        sum += this.zhushicost[i];
      }
      for (var i = 0; i < this.xiaochicost.length; i++) {
        sum += this.xiaochicost[i];
      }
      for (var i = 0; i < this.xicancost.length; i++) {
        sum += this.xicancost[i];
      }
      for (var i = 0; i < this.hongbeicost.length; i++) {
        sum += this.hongbeicost[i];
      }
      for (var i = 0; i < this.yinpincost.length; i++) {
        sum += this.yinpincost[i];
      }
      this.sumCost = sum;
    },

    handleClick () {
      //        console.log(tab, event);
    }


    //改名

  },
  components: {
    'menu_table': MenuTable
  }
}
</script>
<style>
.menu_list > .header,
.footer {
  background-color: #ececec;
  margin-top: 10px;
  padding-left: 5px;
  display: flex;
  justify-content: flex-start;
}

.article_list > .main {
  /*justify-content: flex-start;*/
  display: flex;
  flex-direction: column;
  padding-left: 0px;
  background-color: #fff;
  padding-top: 0px;
  margin-top: 8px;
}
</style>



