<template>
  <div>
    <div style="display: flex; justify-content: flex-start">
      <el-input
        placeholder="通过菜品搜索该分类下的菜..."
        prefix-icon="el-icon-search"
        v-model="keywords"
        style="width: 400px"
        size="mini"
      >
      </el-input>
      <el-button
        type="primary"
        icon="el-icon-search"
        size="mini"
        style="margin-left: 3px"
        @click="searchClick"
        >搜索
      </el-button>
      <el-col :span="6"></el-col>
      <el-col :span="1" style="color: black">应付</el-col>
      <el-col :span="1" style="color: red">{{ sumPrice }}</el-col>
      <el-col :span="1" style="color: black">元</el-col>
    </div>
    <!--<div style="width: 100%;height: 1px;background-color: #20a0ff;margin-top: 8px;margin-bottom: 0px"></div>-->
    <el-table
      ref="multipleTable"
      :data="menus"
      :row-key="getRowKey"
      tooltip-effect="dark"
      style="width: 100%; overflow-x: hidden; overflow-y: hidden"
      max-height="390"
      @selection-change="handleSelectionChange"
      v-loading="loading"
    >
      <el-table-column
        type="selection"
        width="35"
        align="left"
        :reserve-selection="true"
      >
      </el-table-column>
      <el-table-column label="菜品" width="200" align="left">
        <template slot-scope="scope"
          ><span
            style="color: #409eff; cursor: pointer"
            @click="itemClick(scope.row)"
            >{{ scope.row.title }}</span
          >
        </template>
      </el-table-column>
      <el-table-column label="价格" width="140" align="left">
        <template slot-scope="scope">{{ scope.row.price }}</template>
      </el-table-column>

      <el-table-column prop="img" label="图片" width="150" align="center">
        <template v-slot:default="scope">
          <el-image :src="scope.row.location" />
        </template>
      </el-table-column>

      <el-table-column
        prop="introduction"
        label="介绍"
        width="500"
        align="center"
      >
      </el-table-column>
    </el-table>
    <div class="menu_table_footer">
      <!-- <el-button type="danger" size="mini" style="margin: 0px;" v-show="this.menus.length>0"
                 :disabled="this.selItems.length==0" @click="deleteMany">批量删除
      </el-button> -->
      <span class="menus">shao_菜馆 <i class="el-icon-knife-fork"></i></span>
      <el-pagination
        background
        :page-size="pageSize"
        layout="prev, pager, next"
        :total="totalCount"
        @current-change="currentChange"
        v-show="this.menus.length > 0"
      >
      </el-pagination>
    </div>
  </div>
</template>
 

<script>
import { putRequest } from '../utils/api'
import { getRequest } from '../utils/api'
//  import Vue from 'vue'
//  var bus = new Vue()

export default {
  data () {
    return {
      menus: [],
      historymenus: [],
      selItems: [],
      setTitles: [],
      loading: false,
      currentPage: 1,
      totalCount: -1,
      pageSize: 6,
      keywords: '',
      dustbinData: [],
      setPage: [],
      //
      temp: 0

    }
  },
  // updated:function(){
  //   // if(this.temp==1){
  //   //   this.temp=0;
  //   // }
  //   this.$nextTick(()=>{

  //   })
  // },
  mounted: function () {
    var _this = this;
    this.loading = true;

    this.loadMenus(1, this.pageSize);
    var _this = this;
    //选项卡操作  
    window.bus.$on('menuTableReload', function () {
      _this.loading = true;
      _this.loadMenus(_this.currentPage, _this.pageSize);
    });

  },

  methods: {


    getRowKey (row) {
      return row.mid;
    },
    deleteRow () {
      this.$refs.multipleTable.clearSelection();
      // this.historymenus.forEach(item => {
      //   if (item.title === title) {
      //     console.log(this.$refs.multipleTable.toggleRowSelection);
      //     console.log("true");
      //     console.log(title);

      //   }
      // })  
    },
    //search
    searchClick () {
      this.loadMenus(1, this.pageSize);
    },
    itemClick (row) {
      this.$router.push({ path: '/MenuDetail', query: { mid: row.mid, img: row.location } })
    },
    //submit 按钮

    //翻页
    currentChange (currentPage) {
      this.currentPage = currentPage;
      this.loading = true;
      this.loadMenus(currentPage, this.pageSize);
    },
    //加载页面
    loadMenus (page, count) {
      var _this = this;
      var url = "/menus/all?state=" + this.state + "&page=" + page + "&count=" + count + "&keywords=" + this.keywords;
      getRequest(url).then(resp => {
        _this.loading = false;
        if (resp.status == 200) {
          _this.menus = resp.data.menus;
          // console.log(this.historymenus);

          _this.totalCount = resp.data.totalCount;
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }, resp => {
        _this.loading = false;
        if (resp.response.status == 403) {
          _this.$message({ type: 'error', message: resp.response.data });
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }).catch(resp => {
        //压根没见到服务器
        _this.loading = false;
        _this.$message({ type: 'error', message: '数据加载失败!' });
      })
    },
    //submit 选择全部
    handleSelectionChange (val) {
      this.selItems = val;
      // var selItems = this.selItems;
      // this.dustbinData.push(selItems[i].id);
      // this.setTitles.push(selItems[i].title);
      // console.log(this.dustbinData)
      // this.deleteToDustBin(selItems[0].state);
      //func: 是父组件指定的传数据绑定的函数，this.msg:子组件给父组件传递的数据

      this.setTitles = [];
      var prices = [];
      var costs = [];
      for (var i = 0; i < this.selItems.length; i++) {
        this.setTitles.push(this.selItems[i].title);
        // this.setTitles.push(this.selItems[i].price);
        prices.push(this.selItems[i].price);
        costs.push(this.selItems[i].cost);
      }
      // this.$emit("func",this.setTitles);

      if (this.state == 0) {
        // this.$emit("getrecai",this.setTitles,priceSum);
        this.$emit('getrecai', this.setTitles);
        this.$emit('getrecaiprice', prices);
        this.$emit('getrecaicost', costs);
      } else if (this.state == 1) {
        this.$emit('getliangcai', this.setTitles);
        this.$emit('getliangcaiprice', prices);
        this.$emit('getliangcaicost', costs);
      } else if (this.state == 2) {
        this.$emit('gettanggeng', this.setTitles);
        this.$emit('gettanggengprice', prices);
        this.$emit('gettanggengcost', costs);
      } else if (this.state == 3) {
        this.$emit('getzhushi', this.setTitles);
        this.$emit('getzhushiprice', prices);
        this.$emit('getzhushicost', costs);
      } else if (this.state == 4) {
        this.$emit('getxiaochi', this.setTitles);
        this.$emit('getxiaochiprice', prices);
        this.$emit('getxiaochicost', costs);
      } else if (this.state == 5) {
        this.$emit('getxican', this.setTitles);
        this.$emit('getxicanprice', prices);
        this.$emit('getxicancost', costs);

      } else if (this.state == 6) {
        this.$emit('gethongbei', this.setTitles);
        this.$emit('gethongbeiprice', prices);
        this.$emit('gethongbeicost', costs);

      } else {
        this.$emit('getyinpin', this.setTitles);
        this.$emit('getyinpinprice', prices);
        this.$emit('getyinpincost', costs);

      }



    },
    //submit按钮 添加单个
    handleDelete (index, row) {
      this.dustbinData.push(row.id);
      this.deleteToDustBin(row.state);
    },
    //submit 添加操作
    deleteToDustBin (state) {
      var _this = this;
      this.$confirm(state != 2 ? '将该文件放入回收站，是否继续?' : '永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        _this.loading = true;
        var url = '';
        if (_this.state == -2) {
          url = "/admin/article/dustbin";
        } else {
          url = "/article/dustbin";
        }
        putRequest(url, { aids: _this.dustbinData, state: state }).then(resp => {
          if (resp.status == 200) {
            var data = resp.data;
            _this.$message({ type: data.status, message: data.msg });
            if (data.status == 'success') {
              window.bus.$emit('menuTableReload')//通过选项卡都重新加载数据
            }
          } else {
            _this.$message({ type: 'error', message: '删除失败!' });
          }
          _this.loading = false;
          _this.dustbinData = []
        }, resp => {
          _this.loading = false;
          _this.$message({ type: 'error', message: '删除失败!' });
          _this.dustbinData = []
        });
      }).catch(() => {
        _this.$message({
          type: 'info',
          message: '已取消删除'
        });
        _this.dustbinData = []
      });
    },



  },

  props: ['state', 'activeName', 'showRestore', 'sumPrice']
}
</script>
<style type="text/css">
.menu_table_footer {
  display: flex;
  box-sizing: content-box;
  padding-top: 10px;
  padding-bottom: 0px;
  /* 下方菜品样式 */
  padding-left: 40px;
  margin-bottom: 0px;
  text-align: center;
  justify-content: space-between;
  color: sienna;
}
</style>



