<template>
  <div v-show="isAdmin">
    <el-container class="menu_list">
      <el-main class="main">
        <el-tabs @tab-click="handleClick" type="border-card">
          <el-tab-pane label="热菜" name="recai">
            <menu_table state="0"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="凉菜" name="liangcai">
            <menu_table state="1"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="汤羹" name="tanggeng">
            <menu_table state="2"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="主食" name="zhushi">
            <menu_table state="3"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="小吃" name="xiaochi">
            <menu_table state="4"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="西餐" name="xican">
            <menu_table state="5"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="烘焙" name="hongbei">
            <menu_table state="6"></menu_table>
          </el-tab-pane>
          <el-tab-pane label="饮品" name="yinpin">
            <menu_table state="7"></menu_table>
          </el-tab-pane>
        </el-tabs>
      </el-main>
      <el-footer>
        <!-- Form -->
        <el-dialog title="新增菜品" :visible.sync="dialogFormVisible">
          <el-form :model="menu">
            <el-form-item label="菜名">
              <el-input v-model="menu.title" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="价格">
              <el-input v-model="menu.price" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="成本">
              <el-input v-model="menu.cost" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="介绍">
              <el-input
                v-model="menu.introduction"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item label="菜品类型">
              <el-select v-model="curtype" placeholder="请选择菜品类型">
                <el-option
                  v-for="(item, index) in type"
                  :key="index"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <br />
          <p style="text-align: left">上传图片(限加一张)</p>
          <br />
          <el-upload
            action=""
            :on-change="handleelchange"
            :auto-upload="false"
            list-type="picture-card"
            :limit="1"
          >
            <i class="el-icon-plus"></i>
          </el-upload>

          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="addMenu()">确 定</el-button>
          </div>
        </el-dialog>

        <el-button type="danger" size="mini" @click="dialogFormVisible = true"
          >新增菜品</el-button
        >

        <br />
      </el-footer>
    </el-container>
  </div>
</template>
<script>
import MenuManage from '@/components/MenuManage'
import { getRequest, postRequest } from '../utils/api'
import { uploadFileRequest } from '../utils/api'
export default {
  mounted: function () {
    var _this = this;
    getRequest("/isAdmin").then(resp => {
      if (resp.status == 200) {
        _this.isAdmin = resp.data;
        if(_this.isAdmin==false)
        _this.$message({ type:"error", message: '您不是管理员' });
      }
    });

  },
  data () {
    return {
      centerDialogVisible: false,
      isAdmin: false,

      type: ['热菜', '凉菜', '汤羹', '主食', '小吃', '西餐', '烘焙', '饮品'],
      curtype: '热菜',
      //
      menu: {
        title: '',
        price: '',
        cost: '',
        introduction: '',
      },
      dialogFormVisible: false,
      imgurl: ''
    };
  },
  methods: {
    handleLimit (file, fileList) {

    },

    handleelchange (file, fileList) {

      this.$forceUpdate();
      console.log(file, fileList)
      var formdata = new FormData()
      // let file = event.target.files[0];
      fileList.map(item => { //fileList本来就是数组，就不用转为真数组了
        formdata.append("file", item.raw)
        //将每一个文件图片都加进formdata
      })
      uploadFileRequest("/upload/img", formdata).then(res => {
        console.log(res)
        this.imgurl = res.data.msg;
        console.log(this.imgurl)
      })
      // this.imgurl = JSON.parse(window.sessionStorage.getItem('filePath'))
      // console.log(this.imgurl.data.msg)
    },


    addMenu () {
      var _this = this;
      var i = 0;
      for (; i < this.type.length; i++) {
        if (this.type[i] == this.curtype) {
          break;
        }
      }
      postRequest('/menus/addOne', {
        title: _this.menu.title,
        price: _this.menu.price,
        cost: _this.menu.cost,
        introduction: _this.menu.introduction,
        tid: i,
        location: _this.imgurl

      }).then(resp => {
        if (resp.status == 200) {
          var data = resp.data;
          _this.$message({ type: data.status, message: data.msg });
          if (data.status == 'success') {
            window.bus.$emit('blogTableReload')//通过选项卡都重新加载数据
            this.menu = [];
            this.dialogFormVisible = false;
          }
        } else {
          _this.$message({ type: data.status, message: data.msg });
        }
        _this.loading = false;
      }, resp => {
        _this.loading = false;
        _this.$message({ type: 'error', message: '输入信息不得为空,输入信息必须合法!' });
      });
    },

    //删除菜品
    // handleClose0(tag) {
    //   this.recai.splice(this.recai.indexOf(tag), 1);
    //   this.$refs.recaitable.deleteRow(tag);
    // },
    //获取菜品


    handleClick () {
      //        console.log(tab, event);

    }


    //改名
  },

  components: {
    'menu_table': MenuManage
  }
}
</script>
<style>
.menu_list > .header,
.footer {
  background-color: #ececec;
  margin-top: 10px;
  padding-left: 5px;
  display: flex;
  justify-content: flex-start;
}

.article_list > .main {
  /*justify-content: flex-start;*/
  display: flex;
  flex-direction: column;
  padding-left: 0px;
  background-color: #fff;
  padding-top: 0px;
  margin-top: 8px;
}
</style>



