<template>
  <div>
    <el-table
      :data="orders"
      style="width: 100%"
      :default-sort="{ prop: 'date', order: 'descending' }"
    >
      <el-table-column prop="publishDate" label="订购日期" sortable width="180">
        <template slot-scope="scope">{{
          formatDate(scope.row.publishDate)
        }}</template>
      </el-table-column>
      <el-table-column prop="content" label="订单" >
      </el-table-column>
      <el-table-column prop="customer" label="姓名" sortable width="180">
      </el-table-column>
      <el-table-column prop="phone" label="电话" >
      </el-table-column>
      <el-table-column prop="address" label="地址"  >
      </el-table-column>
      <el-table-column prop="price" label="价格" sortable width="180" > </el-table-column>
      <!-- <el-button type="danger" size="mini" style="margin: 0px;" v-show="this.menus.length>0"
                 :disabled="this.selItems.length==0" @click="deleteMany">批量删除
      </el-button> -->
    </el-table>
    <br />
    <el-pagination
      background
      :page-size="pageSize"
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="currentChange"
       v-show="this.orders.length > 0"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getRequest } from '../utils/api'
import moment from 'moment'
export default {
  mounted () {
    var _this = this;
    this.loading = true;

    this.loadOrders(1, this.pageSize);
  },
  data () {
    return {
      currentPage: 1,
      totalCount: -1,
      pageSize: 6,
      orders: [],
     
    }
  },
  methods: {
    formatDate (time) {
      return moment(time).format('YYYY-MM-DD HH:mm:ss')
    },
    formatter (row, column) {
      return row.address;
    },
    currentChange (currentPage) {
      this.currentPage = currentPage;
      this.loading = true;
      this.sumItems = this.selItems;
      this.loadOrders(currentPage, this.pageSize);
    },
    loadOrders (page, count) {
      var _this = this;
      var url = "/myHistoryOrders?page=" + page + "&count=" + count;
      getRequest(url).then(resp => {
        _this.loading = false;
        if (resp.status == 200) {
          _this.orders = resp.data.orders;
          _this.totalCount = resp.data.totalCount;
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }, resp => {
        _this.loading = false;
        if (resp.response.status == 403) {
          _this.$message({ type: 'error', message: resp.response.data });
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }).catch(resp => {
        //压根没见到服务器
        _this.loading = false;
        _this.$message({ type: 'error', message: '数据加载失败!' });
      })
    },
  }
}
</script>
<style scoped>
.menu_table_footer {
  display: flex;
  box-sizing: content-box;
  padding-top: 10px;
  padding-bottom: 0px;
  /* 下方菜品样式 */
  padding-left: 40px;
  margin-bottom: 0px;
  text-align: center;
  justify-content: space-between;
  color: sienna;
}
</style>



