<template>
  <div v-show="isAdmin">
    <span class="demonstration">选择日期范围</span>
    &emsp;
    <el-date-picker
      v-model="value1"
      type="datetimerange"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      value-format="yyyy-MM-dd HH:mm:ss"
      :default-time="['00:00:00']"
    >
    </el-date-picker>
    &emsp;
    <el-button @click="what">查询</el-button>
    <i class="el-icon-info"  @click="open2"></i>
    <br />
    <br />
    <span></span>
    营业额:<span style="color: red">&emsp;{{ sumPrice }}&emsp;</span
    >元
    <br />
    利润:<span style="color: red">&emsp;{{ profit }}&emsp;</span
    >元
    <br>
    <br>
    <div
      style="text-align: left"
      v-for="(user, index) in userList"
      :key="index"
    >
      <el-descriptions direction="vertical" :column="4" border>
        <el-descriptions-item label="用户名">{{
          user.username
        }}</el-descriptions-item>
        <el-descriptions-item label="手机号">{{
          user.phone
        }}</el-descriptions-item>
        <el-descriptions-item label="完成单数" :span="2">{{
          user.count
        }}</el-descriptions-item>
      </el-descriptions>
      <br />
    </div>
    <el-pagination
      background
      :page-size="pageSize"
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="currentChange"
      v-show="this.userList.length > 0"
    >
    </el-pagination>
  </div>
</template>
<script>
import { getRequest } from '../utils/api'

export default {
  mounted: function () {
    var _this = this;
    this.loading = true;
    getRequest("/isAdmin").then(resp => {
      if (resp.status == 200) {
        _this.isAdmin = resp.data;
        if(_this.isAdmin==false)
        _this.$message({ type:"error", message: '您不是管理员' });
      }
    });
    this.loadMenus(1, this.pageSize);

  },
  data () {
    return {

      value1: [],
      loading: false,
      currentPage: 1,
      pageSize: 3,
      totalCount: -1,

      userList: [],
      sumPrice: '',
      profit: '',

      isAdmin:false
    }
  },
  methods: {
    open2 () {
      this.$notify({
        title: '提示',
        message: '可以用来查询这段时间骑手完成的订单数,和总的营业额，利润',
        duration: 0
      });
    },
    what () {
      console.log(this.value1[0]);
      console.log(this.value1[1]);
      this.loadMenus(this.currentPage, this.pageSize);
    },
    currentChange (currentPage) {
      this.currentPage = currentPage;
      this.loading = true;
      this.loadMenus(currentPage, this.pageSize);
    },
    //加载页面
    loadMenus (page, count) {
      var _this = this;
      var url = "/whatMoney?startDate=" + this.value1[0] + "&endDate=" + this.value1[1] + "&page=" + page + "&count=" + count;
      getRequest(url).then(resp => {
        _this.loading = false;
        if (resp.status == 200) {
          _this.userList = resp.data.userList;
          _this.profit = resp.data.profit;
          _this.sumPrice = resp.data.sumPrice;
          // console.log(this.historymenus);

          _this.totalCount = resp.data.totalCount;
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }, resp => {
        _this.loading = false;
        if (resp.response.status == 403) {
          _this.$message({ type: 'error', message: resp.response.data });
        } else {
          _this.$message({ type: 'error', message: '数据加载失败!' });
        }
      }).catch(resp => {
        //压根没见到服务器
        _this.loading = false;
        _this.$message({ type: 'error', message: '数据加载失败!' });
      })
    },
  }
}
</script>




