<template>
  <div v-show="isAdmin">
    <el-table
      :data="orders"
      style="width: 100%"
      :default-sort="{ prop: 'date', order: 'descending' }"
      :cell-style="cellStyle"
    >
      <el-table-column prop="publishDate" label="订购日期" sortable width="180">
        <template slot-scope="scope">{{
          formatDate(scope.row.publishDate)
        }}</template>
      </el-table-column>
      <el-table-column prop="content" label="订单" width="200">
      </el-table-column>
      <el-table-column prop="customer" label="姓名" sortable width="140">
      </el-table-column>
      <el-table-column prop="phone" label="电话" width="140"> </el-table-column>
      <el-table-column prop="address" label="地址" width="180">
      </el-table-column>
      <el-table-column prop="price" label="价格" sortable width="180">
      </el-table-column>
      <el-table-column prop="limitTime" label="预计时间" sortable width="180">
      </el-table-column>
      <el-table-column label="状态" width="140" align="left">
        <template slot-scope="scope">{{ type[scope.row.status] }}</template>
      </el-table-column>
      <el-table-column label="接单人信息" width="140" align="left">
        <template slot-scope="scope"
          >接单人姓名:{{ scope.row.user.username }}&emsp;接单人电话:{{
            scope.row.user.phone
          }}</template
        >
      </el-table-column>
    </el-table>

    <br />
    <el-pagination
      background
      :page-size="pageSize"
      layout="prev, pager, next"
      :total="totalCount"
      @current-change="currentChange"
      v-show="this.orders.length > 0"
    >
    </el-pagination>
  </div>
</template>

<script>
import { getRequest } from '../utils/api'
import moment from 'moment'
export default {
  mounted () {
    var _this = this;
    this.loading = true;
    getRequest("/isAdmin").then(resp => {
      if (resp.status == 200) {
        _this.isAdmin = resp.data;
        if(_this.isAdmin==false)
        _this.$message({ type:"error", message: '您不是管理员' });
      }
    });
    this.loadOrders(1, this.pageSize);
    if (this.timer) {
        clearInterval(this.timer);
    } else {
        this.timer = setInterval(() => {
            this.queryInfo(); // methods中请求数据的方法
        }, 3000);
    }
 

  },

  data () {
    return {
      currentPage: 1,
      totalCount: -1,
      pageSize: 6,
      orders: [],
      type: ['配送中', '已送达', '暂未配送'],

      timer: null,
      isAdmin:false

    }
  },
  destroyed () {
    clearInterval(this.timer);
  },

  methods: {
  queryInfo() {
    //数据请求
    this.loadOrders(this.currentPage, this.pageSize);
  },
  cellStyle(row, column, rowIndex, columnIndex) {
    if (row.column.label === "状态" && row.row.status == 0) {
      return 'color:blue' //颜色可以自己选择
    }

    if (row.column.label === "状态" && row.row.status == 1) {
      return 'color:green'
    }
    if (row.column.label === "状态" && row.row.status == 2) {
      return 'color:red'
    }


  },

  formatDate(time) {
    return moment(time).format('YYYY-MM-DD HH:mm:ss')
  },
  formatter(row, column) {
    return row.address;
  },
  currentChange(currentPage) {
    this.currentPage = currentPage;
    this.loading = true;
    this.sumItems = this.selItems;
    this.loadOrders(currentPage, this.pageSize);
  },
  loadOrders(page, count) {
    var _this = this;
    var url = "/getAllOrders?page=" + page + "&count=" + count;
    getRequest(url).then(resp => {
      _this.loading = false;
      if (resp.status == 200) {
        _this.orders = resp.data.orders;
        _this.totalCount = resp.data.totalCount;
      } else {
        _this.$message({ type: 'error', message: '数据加载失败!' });
      }
    }, resp => {
      _this.loading = false;
      if (resp.response.status == 403) {
        _this.$message({ type: 'error', message: resp.response.data });
      } else {
        _this.$message({ type: 'error', message: '数据加载失败!' });
      }
    }).catch(resp => {
      //压根没见到服务器
      _this.loading = false;
      _this.$message({ type: 'error', message: '数据加载失败!' });
    })
  },
}
}
</script>
<style scoped>
.menu_table_footer {
  display: flex;
  box-sizing: content-box;
  padding-top: 10px;
  padding-bottom: 0px;
  /* 下方菜品样式 */
  padding-left: 40px;
  margin-bottom: 0px;
  text-align: center;
  justify-content: space-between;
  color: sienna;
}
.what {
  color: red;
  font-size: 100px;
}
</style>



