import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Home from '@/components/Home'
import MenuList from '@/components/MenuList'
import PostArticle from '@/components/PostArticle'
import MyOrder from '@/components/MyOrder'
import MenuDetail from '@/components/MenuDetail'
import Register from '@/components/Register'
import Message from '@/components/Message'
import ReceiveList from '@/components/ReceiveList'
import MenuMangeList from '@/components/MenuMangeList'
import MyCurOrder from '@/components/MyCurOrder'
import OrdersManage from '@/components/OrdersManage'
import MoneyManage from '@/components/MoneyManage'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/ms',
      name: '登录',
      hidden: true,
      component: Message
    },
    {
      path: '/',
      name: '登录',
      hidden: true,
      component: Login
    }, {
      path: '/re',
      component: Register,
      name: '注册',
      hidden: true
    }, {
      path: '/home',
      name: '',
      component: Home,
      hidden: true
    },
    {
      path: '/MenuList',
      component: Home,
      name: '点菜大厅',
      children: [{
        path: '/user',
        iconCls: 'el-icon-food',
        name: '点菜大厅',
        component: MenuList,
        meta: {
          keepAlive: true
        }
      }]
    },
    {
      path: '/home',
      component: Home,
      name: '优惠服务',
      iconCls: 'el-icon-s-goods',
      children: [{
        path: '/MenuList',
        name: '优惠券商店',
        component: MenuList,
        meta: {
          keepAlive: true
        }
      }, {
        path: '/postArticle',
        name: '我的优惠券',
        component: PostArticle,
        meta: {
          keepAlive: false
        }
      }, {
        path: '/MenuDetail',
        name: '菜品详情',
        component: MenuDetail,
        hidden: true,
        meta: {
          keepAlive: false
        }
      }, ]
    },

    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MyCurOrder',
        iconCls: 'el-icon-tickets',
        name: '我的订单(进行中)',
        component: MyCurOrder
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MyOrder',
        iconCls: 'el-icon-tickets',
        name: '历史订单',
        component: MyOrder
      }]
    },

    {
      path: '/home',
      component: Home,
      children: [{
        path: '/jiedan',
        iconCls: 'el-icon-document-add',
        name: '骑手接单',
        component: ReceiveList
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/MenuMangeList',
        iconCls: 'el-icon-notebook-2',
        name: '菜品管理',
        component: MenuMangeList
      }]
    },
    {
      path: '/Home',
      component: Home,
      children: [{
        path: '/OrdersManage',
        iconCls: 'el-icon-collection',
        name: '订单管理',
        component: OrdersManage
      }]
    },
     {
       path: '/Home',
       component: Home,
       children: [{
         path: '/MoneyManage',
         iconCls: 'el-icon-collection',
         name: '财务管理',
         component: MoneyManage
       }]
     }

  ]
})


